<template>
<div class="container-fluid basic-info-wrapper pt-0" v-if="basicInfo && Object.keys(basicInfo).length">
    <Gap :height="'1.1rem'" />
    <div class="row m-0 join-step-row-content">
        <RadioInput isRequired :label="$t('Joining.CurrentStatus')" v-model="basicInfo.userStatus" :options="[
            {name: $t(`Joining.${$CYconstants.STATUS.IN_PROGRESS}`), value: $CYconstants.STATUS.IN_PROGRESS},
            {name: $t(`Joining.${$CYconstants.STATUS.WAITING}`), value: $CYconstants.STATUS.WAITING}]" :name="'statusSelector'" :value="$CYconstants.STATUS.WAITING"></RadioInput>
    </div>
    <div class="row m-0 join-step-row-content">
        <DateTimeInput isRequired :label="$t('Joining.AvailableDate')" v-model="basicInfo.availableDate"></DateTimeInput>
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.JobOfHope1')" />
        </div>
        <div class="col p-0 cy-col-pad first">
            <SelectInput :options="jobs1_part1" v-model="basicInfo.job11" />
        </div>
        <div class="col p-0 cy-col-pad last">
            <SelectInput :options="jobs1_part2" v-model="basicInfo.job12" />
        </div>
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.JobOfHope2')" />
        </div>
        <div class="col p-0 cy-col-pad first">
            <SelectInput :options="jobs2_part1" v-model="basicInfo.job21" />
        </div>
        <div class="col p-0 cy-col-pad last">
            <SelectInput :options="jobs2_part2" v-model="basicInfo.job21" />
        </div>
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.HopefulWorkPlace1')" />
        </div>
        <div class="col p-0 cy-col-pad first">
            <SelectInput :options="places1_part1" v-model="basicInfo.location11" />
        </div>
        <template v-if="basicInfo.location11 == $CYconstants.COUNTRY_CODE.KOREA">
            <div class="col p-0 cy-col-pad">
                <SelectInput :options="places1_part2" v-model="basicInfo.location12" />
            </div>
            <div class="col p-0 cy-col-pad last">
                <SelectInput :options="places1_part3" v-model="basicInfo.location13" />
            </div>
        </template>
        <template v-else>
            <div class="col p-0 cy-col-pad last">
                <TextInput v-model="basicInfo.location14" :name='"HopefulWorkPlace1Two"' />
            </div>
        </template>
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.HopefulWorkPlace2')" />
        </div>
        <div class="col p-0 cy-col-pad first">
            <SelectInput :options="places2_part1" v-model="basicInfo.location21" />
        </div>
        <template v-if="basicInfo.location21 == $CYconstants.COUNTRY_CODE.KOREA">
            <div class="col p-0 cy-col-pad">
                <SelectInput :options="places2_part2" v-model="basicInfo.location22" />
            </div>
            <div class="col p-0 cy-col-pad last">
                <SelectInput :options="places2_part3" v-model="basicInfo.location23" />
            </div>
        </template>
        <template v-else>
            <div class="col p-0 cy-col-pad last">
                <TextInput v-model="basicInfo.location24" :name='"HopefulWorkPlace2Two"' />
            </div>
        </template>
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.TotalITExperience')" />
        </div>
        <div class="col p-0 cy-col-pad first">
            <SelectInput v-model="basicInfo.itYear" :options="Years" />
        </div>
        <div class="col p-0 cy-col-pad last">
            <SelectInput v-model="basicInfo.itMonth" :options="Months" />
        </div>
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.SAPExperience')" />
        </div>
        <div class="col p-0 cy-col-pad first">
            <SelectInput v-model="basicInfo.sapYear" :options="Years" />
        </div>
        <div class="col p-0 cy-col-pad last">
            <SelectInput v-model="basicInfo.sapMonth" :options="Months" />
        </div>
    </div>

    <div class="row m-0 join-step-row-content">
        <Button :label="$t('Joining.SAVE')" :wrapperClass="'w-100'" @click.native="updateBasicInfo"></Button>
    </div>
</div>
</template>

<script>
import TextInput from '@/components/Common/Input/TextInput';
import DateTimeInput from '@/components/Common/Input/DateTimeInput';
import RadioInput from '@/components/Common/Input/RadioInput';
import Button from '@/components/Common/Button/Button';
import SelectInput from '@/components/Common/Input/SelectInput';
import InputLabel from '@/components/Common/Input/InputLabel';
import moment from 'moment';
import {
    CODE_COMMON,
    COUNTRY_CODE
} from '@/constants'
import {
    SHOW_POPUP_BUTTON
} from '@/mutations/Common.mutation';

export default {
    name: "BasicInformation",
    components: {
        TextInput,
        DateTimeInput,
        RadioInput,
        Button,
        SelectInput,
        InputLabel,
    },
    data: function () {
        return {
            basicInfo: null,

            // job of hope 1
            jobs1_part1: [],
            jobs1_part2: [],

            // job of hope 2
            jobs2_part1: [],
            jobs2_part2: [],

            // hopeful work place 1
            places1_part1: [],
            places1_part2: [],
            places1_part3: [],

            // hopeful work place 2
            places2_part1: [],
            places2_part2: [],
            places2_part3: [],

            Months: [],
            Years: [],
            locale: this.$i18n.locale
        }
    },
    async created() {
        await this.getData();
    },
    watch: {

    },
    methods: {
        updateBasicInfo: function () {
            return this.$router.push('/personal-information');
        },
        async getData() {
            this.basicInfo = await this.$store.state.MyPage.myCareer;

            this.basicInfo['availableDate'] = moment(this.basicInfo.availDt).format('YYYY.MM.DD');
            this.basicInfo['itYear'] = +this.basicInfo.itCareerYear + '';
            this.basicInfo['itMonth'] = +this.basicInfo.itCareerMonth + '';
            this.basicInfo['sapYear'] = +this.basicInfo.sapCareerYear + '';
            this.basicInfo['sapMonth'] = +this.basicInfo.sapCareerMonth + '';

            this.jobs1_part1 = this.jobs2_part1 = await this.getSelectList(CODE_COMMON.B00000);
            this.jobs1_part2 = await this.getJobDetail(this.basicInfo.job11);
            this.jobs2_part2 = await this.getJobDetail(this.basicInfo.job21);

            this.places1_part1 = this.places2_part1 = await this.getSelectList(CODE_COMMON.C00000); // for location11, location21 
            this.places1_part2 = await this.getSelectList(this.basicInfo.location11);
            this.places2_part2 = await this.getSelectList(this.basicInfo.location11);
            this.places1_part3 = await this.getSelectList(this.basicInfo.location12);
            this.places2_part3 = await this.getSelectList(this.basicInfo.location22);

            this.Months = this.$CYconstants.MONTHS.map(m => ({
                value: m + "",
                name: m + " " + (m > 1 ? this.$t('BasicInfo.Months') : this.$t('BasicInfo.Month'))
            }));
            this.Years = this.$CYconstants.EXP_YEARS.map(y => ({
                value: y + "",
                name: y + " " + (y > 1 ? this.$t('BasicInfo.Years') : this.$t('BasicInfo.Year'))
            }));
        },
        capitalizeFirstLetter(string) {
            return string.charAt(0).toUpperCase() + string.slice(1);
        },
        async getJobDetail(job) {
            if (job) {
                return await this.getSelectList(job)
            }
        },
        async getSelectList(code) {
            try {
                if (code) {
                    let info = await this.$ApiService.doGetCommonInfo(code);
                    if (info && info.data && info.data.RST_DATA) {
                        let result = info.data.RST_DATA.map(item => {
                            return {
                                value: item.cmmCd,
                                name: item[`name${this.capitalizeFirstLetter(this.locale)}`]
                            }
                        })
                        return result;
                    }
                }
            } catch (error) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, error.message);
            }
        }
    }
}
</script>

<style lang="scss">
.basic-info-wrapper {
    .cy-col-pad {
        &.first {
            margin-left: 0;
        }

        &.last {
            margin-right: 0;
        }

        margin-left: .75rem;
        margin-right: .75rem;
    }
}
</style>
