<template>
    <div class="d-flex flex-column select-input-wrapper" :class="wrapperClass">
        <label class="text-input-label" :class="isInline ? 'label-take-place' : ''">{{label}}<span class="text-danger">{{isRequired ? ' *':''}}</span></label>
        <b-dropdown :text="selectedName" class="m-md-2">
            <b-dropdown-item class="select-input-item" v-for="(option, key) in options" :key="key" @click="() => selectOption(option.value)">{{option.name}}</b-dropdown-item>
        </b-dropdown>
        <!-- <input :type="`${isPassword ? 'password' : 'text'}`" class="text-input" :value="value" @change="changeLocal"> -->
        <div class="text-danger error">{{error}}</div>
    </div>
</template>
<script>
export default {
    name: 'SelectInput',
    props: {
        label: String,
        value: String,
        type: String,
        error: String,
        options: {
            type: Array,
            default: () => [{name: '', value: ''}],
        },
        wrapperClass: String,
        isRequired: {
            type: Boolean,
            default: false,
        },
        isInline: {
            type: Boolean,
            default: false,
        }
    },
    model: {
        event: 'change'
    },
    methods: {
        changeLocal: function (event) {
            this.$emit('change', event.target.value);
        },
        selectOption: function (value) {
            this.$emit('change', value);
        }
    },
    computed: {
        selectedName() {
            let selectedOpt = this.options.filter(opt => opt.value == this.value);
            if (Array.isArray(selectedOpt) && selectedOpt.length > 0) {
                selectedOpt = selectedOpt[0];
                return selectedOpt.name;
            }
            return ''
        }
    }
}
</script>
<style lang="scss">
    @import '@/assets/styles/variables.scss';
    .select-input-wrapper {
        width: 100%;
        .text-input-label {
            font-family: NotoSansKR;
            font-weight: 300;
            font-size: 1.4rem;
            line-height: 2rem;
            color: #7C848D;
            margin-bottom: .7rem;
            &.label-take-place {
                display: block;
                min-height: 2.0rem;
            }
        }
        .dropdown-menu {
            max-height: 15rem;
            overflow-y: scroll;
            width: 100%;
        }
        .error {
            display: inline-block;
            font-size: 1.2rem;
            line-height: 2.0rem;
            width: 100%;
            margin-bottom: .3rem;
            min-height: 2.0rem;
            position: relative;
        }
        .dropdown-toggle {
            background: transparent;
            border: none;
            border-bottom: 0.05rem solid #E0E0E0;
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: normal;
            font-size: 1.4rem;
            line-height: 2.0rem;
            color: #4F4F4F;
            text-align: left;
            position: relative;
            height: 2.65rem;
            &:after {
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
            }
            box-shadow: none;
        }
        .select-input-item {
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: normal;
            font-size: 1.4rem;
            line-height: 2.0rem;
            color: #4F4F4F;
            text-align: left;
            &:hover {
                background-color: $cy-blue;
                color: white;
            }
        }
        .show > .btn-secondary.dropdown-toggle {
            color: #4F4F4F;
            background-color: transparent;
            border-color: #E0E0E0;
        }
    }
</style>